@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}
html, body, #root{
    height: 100%;
}
body{
    background: #e5E6F0;
    -webkit-font-smoothing: antialiased;
}
body, input, button{
    font-family: Roboto, sans-serif;
}
aside{
    width: 320px;
    background: #ffffff;
    box-shadow: 0 0 14px 0 rgba(0,0,0,0.02);
    border-radius: 2px;
    padding: 30px 20px;
}
aside strong{
    font-size: 20px;
    text-align: center;
    display: block;
    color: #333;
}
aside form{
    margin-top: 30px;
}
aside form .input-block label{
    color: #aaccaa;
    font-size: 14px;
    font-weight: bold;
    display: block;   
}
aside form .input-group .input-block{
    margin-top: 0;
}

aside form .input-block + input.input-block {
    margin-top: 20px;
}
aside form .input-group{
    margin-top: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: 1fr 1fr;
}

aside form .input-block input{
    width: 100%;
    height: 32px;
    font-size: 14px;
    color: #666;
    border: 0px;
    border-bottom: 1px solid #eee;
}
aside form button[type=submit]{
    width: 100%;
    border: 0;
    margin-top: 30px;
    background: #7d40e7;
    border-radius: 2px;
    padding: 15px 20px;
    font-size: 16px;
    font-weight: bold;
    color:#fff;
    cursor: pointer;
    -webkit-transition: background 0.5s;
    transition: background 0.5s;
}
aside form button[type=submit]:hover{
    background: #6931ca;
}
main{
    flex:1 1;
}
main ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;
    list-style: none;
}
@media(max-width: 650px){
    main ul{
        grid-template-columns: 1fr ;
    }
}
li.dev-item{
    background: #ffffff;
    box-shadow: 0 0 14px 0 rgba(0,0,0,0.02);
    border-radius: 2px;
    padding: 20px;
}
li.dev-item header {
    display: flex;
    flex-direction: row;
    align-items: center;
}
li.dev-item header img{
    width: 54px;
    height: 54px;
    border-radius: 50%;
}
li.dev-item header .user-info{
    margin-left: 10px;
}
.user-info strong{
    display: block;
    font-size: 16px;
    color: #333;
}
.user-info spam{
    font-size: 13px;
    color: #9999;
    margin-top: 2px;
}
li.dev-item p{
    color: #6666;
    font-size: 14px;
    line-height: 20px;
    margin:10px 0;
}
li.dev-item a{
    color: #8e4dff;
    font-size: 14px;
    text-decoration: none;
}
li.dev-item a:hover{
    color: #5a2ea6;
}
